import React from 'react'
import './timer.css'

export class Timer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            elapsed: 0,
            seconds: 0,
            minutes: 0
        };
    };
    
    componentDidMount = ()=>{
            this.timer = setInterval(this.tick, 100)
    };

    componentWillUnmount = ()=>{
        clearInterval(this.timer);
    };

    tick = ()=>{
        let elapsed = Math.round(this.state.elapsed / 100);

        if (this.props.game.startTimer) {
            this.setState({
                elapsed: new Date() - this.props.game.startTimer,
                seconds: ((elapsed / 10) - this.state.minuteCounter).toFixed(1)
            })
        } else {
            this.setState({
                elapsed: 0,
                seconds: 0,
                minutes: 0,
                minuteCounter: 0
            })
        }

        if (this.state.seconds > 60) {
            let minutes = this.state.minutes+1;
            this.setState({
                minutes: minutes,
                minuteCounter: this.state.minuteCounter + 60
            })
        }

        if (this.props.game.gameEnd) {
            clearInterval(this.timer);
            const seconds = this.state.seconds;
            const minutes = this.state.minutes;
            const difficult = this.props.game.gameDifficult;
            this.props.pageActions.saveResult(difficult, seconds, minutes);
        }

    };

    render() {
        
        return (
            <div className="timer">
                <p id="minutes" className="time minutes">{this.state.minutes}</p>
                <p className="divider">:</p>
                <p id="seconds" className="time seconds">{this.state.seconds}</p>
            </div>
            //<p>This example was started <b>{seconds} seconds</b> ago.</p>
        )
    }
}


