import React from 'react'
import Rules from './loginScreen-item/rules/rules'
import DataInput from './loginScreen-item/dataInput/dataInput'
import './loginScreen.css'

export class LoginScreen extends React.Component {

    render () {
        return (
            <div className="title-screen">
                <div className="container">
                    <Rules />
                    <DataInput startGame={this.props.startGame} />
            	</div>
            </div>
        )
    }
}



