export const RequestMethod = {
    GET: "GET",
    POST: "POST"
};

export class Data {
  constructor(url) {
    this.url = url;
  }
  
  async request(url, prop) {
    const responce = new Request(url, prop);
    return await this.fetchResponce(responce);
  }

  async fetchResponce(responce) {
    const fetched = await fetch(responce)
    .then((responce)=>{
        return responce.json();
        })
    return fetched;
  }
}


