import React from 'react'
import './cardContainer.css'

export default class CardContainer extends React.Component {
    
    cardSelect = (e) => {
        const startTimer = this.props.startTimer;
        const currentCard = e.target.getAttribute('card_key');
        const selectedCards = this.props.selectCards;
        const matchedCards = this.props.matchedCards;
        const cardsCounter = this.props.cards.length;
        this.props.cardSelect(startTimer, cardsCounter, currentCard, selectedCards, matchedCards);
    }

    render () {
        let cards=[];
        let backPosition = 0;
        const cardsArr = this.props.cards;
        
        switch (this.props.cardType) {
            case '1':
            backPosition = 0;
            break;
            case '2':
            backPosition = 936;
            break;
            case '3':
            backPosition = 468;
            // no default
        }
        cardsArr.forEach((element, i) => {
            cards.push(
                <div 
                className={"card "+(
                    this.props.selectCards.some((item)=>{
                    return element+i === item}) ? "rotate " : ""
                ) + (
                    this.props.matchedCards.some((item)=>{
                    return item === element+i}) ? "hidden" : ""
                    )}
                key={element+i} 
                card_key={element+i} 
                style={{backgroundPosition: backPosition +"px"}} 
                onClick={this.cardSelect.bind(this)} >
                    <div className='cardFace' style={{backgroundPosition: backPosition +"px"}}>
                        <p>{element}</p>
                    </div>
                </div>)
        });

        return (
            <div className='card-container' >
                {cards}
            </div>
        )
    }
}


