import {Data, RequestMethod} from "./Data.class";

class ScoreDataClass extends Data {
    constructor(url) {
      super(url);
    }
  
    async getData() {
      const fetched =  await fetch('https://mmg-server.azurewebsites.net/', {
        headers: {'Content-Type':'application/json'},
      mode: 'cors',
      method: 'GET'
    }).then(function(response) {
      return response.json();
    });
    
      return fetched;
    }

    async sendData(data) {
      const prop = {
        method: RequestMethod.POST,
        body: JSON.stringify(data),
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        mode: 'cors'
      }
      
      const fetched =  await fetch('https://mmg-server.azurewebsites.net/', {
        headers: {'Content-Type':'application/json'},
        mode: 'cors',
        method: 'post',
        body: JSON.stringify({data: data})
      })
      return fetched;
    }
  }

export const ScoreData = new ScoreDataClass('https://mmg-server.azurewebsites.net/');


