import {ScoreData} from '../service/ScoreData'

export const ScoreDataActions = {
    SCORE_DATA: {
        IS_LOADING: "SCORE_DATA.IS_LOADING",
        IS_LOADED: "SCORE_DATA.IS_LOADED"
    }
};

export function startGame(name, email, cardType, difficulty, page) {
    
    const hard = ['A', 'A', 'B', 'B', 'C', 'C', 'D', 'D', 'E', 'E', 'F', 'F', 'G', 'G', 'H', 'H', 'I', 'I', 'J', 'J', 'K', 'K', 'L', 'L'];
	const med = ['A', 'A', 'B', 'B', 'C', 'C', 'D', 'D', 'E', 'E', 'F', 'F', 'G', 'G', 'H', 'H'];
	const low = ['A', 'A', 'B', 'B', 'C', 'C', 'D', 'D'];
	let cards;
	const selectedCards = [];
	const startTimer = 0;
	const gameEnd = false;
	const matchedCards = [];
	const saveScore = false;
	function random(a, b) {
		return Math.random() - 0.5;
      }
      
	switch (difficulty) {
		case 'low':
		cards = low.sort(random);
		break;
		case 'med':
		cards = med.sort(random);
		break;
		case 'hard':
		cards = hard.sort(random);
		// no default
    }
    
    return {
        type: 'START_GAME',
        payload: [name, email, cardType, difficulty, page, cards, selectedCards, startTimer, gameEnd, matchedCards, saveScore]
    }
}

export function cardSelect (startTimer, cardsCounter, currentCard, selectedCard, matchedCards) {
	let selectedCards = selectedCard;
	let timerStart = startTimer;
	let matchCards = matchedCards;
	let gameEnd = false;
	if (!startTimer) {
		timerStart = Date.now()
	}
	if (selectedCards.some((i)=>{
		return i === currentCard
	})) {
		return {
			type: 'CARD_SELECT',
			payload: [timerStart, selectedCards, matchCards, gameEnd]
		}
	}

	switch (selectedCards.length) {
		case (0):
		selectedCards.push(currentCard);
		break;
		case (1):
		selectedCards.push(currentCard);
		if (selectedCards[0][0] === selectedCards[1][0]) {
			matchCards.push(selectedCards[0])
			matchCards.push(selectedCards[1])
			if (matchCards.length === cardsCounter) {
				gameEnd = true;
			}
		}
		break;
		case(2):
		selectedCards = [];
		selectedCards.push(currentCard);
		break;
		// no default
	}
    return {
        type: 'CARD_SELECT',
        payload: [timerStart, selectedCards, matchCards, gameEnd]
    }
}

export function saveResult (difficult, seconds, minutes) {
	let maxScore;
	switch (difficult) {
		case 'low':
		maxScore = 1000;
		break;
		case 'med':
		maxScore = 2000;
		break;
		case 'hard':
		maxScore = 3000;
		// no default
	}
	const score = maxScore - (minutes*60+seconds)*10;

	return {
		type: 'SAVE_RESULT',
		payload: [score, true]
	}
}

export function endGame () {
	const page = 'score';
	return {
		type: 'END_GAME',
		payload: [page]
	}
}

export function sendScoreData(result) {
    return async (dispatch) => {
        dispatch({
			type: 'SCORE_DATA.IS_SENDING',
			payload: ['sending']
        });
		await ScoreData.sendData(result);
        dispatch({
            type: 'SCORE_DATA.IS_SENDED',
            payload: ['sended']
        });
    };
}

export function loadScoreData() {
    return async (dispatch) => {
        dispatch({
			type: 'SCORE_DATA.IS_LOADING',
			payload: ['loading']
        });
		const data = await ScoreData.getData();
        dispatch({
            type: 'SCORE_DATA.IS_LOADED',
            payload: [data, 'loaded']
        });
    };
}

export function prevScore (CurrDispPosition) {
	let displayPosition = CurrDispPosition - 10;
	if (displayPosition < 0) {
		displayPosition = 0;
	}
	return {
		type: 'PREVIOUS_SCORE',
		payload: [displayPosition]
	}
}

export function nextScore (CurrDispPosition, dataLength) {
	let displayPosition = CurrDispPosition + 9;
	if (displayPosition >= dataLength) {
		displayPosition = dataLength - 9;
	}
	return {
		type: 'NEXT_SCORE',
		payload: [displayPosition]
	}
}


