const initialState = {
	name: 'Unknown',
	email: 'Unknown@hero.com'
};
  
export default function user(state = initialState, action) {
	switch (action.type) {
		case 'START_GAME':
			return {
			name: action.payload[0],
			email: action.payload[1]
			}
		default:
			return state;
	}
}


