const initialState = {
	page: 'login',
	cardType: '1',
	gameDifficult: 'low',
	cards: [],
	selectedCards: [],
	matchedCards: [],
	gameEnd: false,
	startTimer: 0,//Date.now()
	score: 0,
	saveScore: false
}

export default function game (state = initialState, action) {

	switch (action.type) {
		case 'START_GAME':
		// return state
		// .set('cardType', action.payload[2])
		// .set('gameDifficult', action.payload[3])
		// .set('page', action.payload[4])
		// .set('cards', action.payload[5])
		// .set('selectedCards', action.payload[6])
		// .set('startTimer', action.payload[7])
		// .set('gameEnd', action.payload[8])
		// .set('matchedCards', action.payload[9])
			return {...state,
				cardType: action.payload[2],
				gameDifficult: action.payload[3],
				page: action.payload[4],
				cards: action.payload[5],
				selectedCards: action.payload[6],
				startTimer: action.payload[7],
				gameEnd: action.payload[8],
				matchedCards: action.payload[9],
				saveScore: action.payload[10]
			}
		case 'CARD_SELECT':
			return {...state,
				startTimer: action.payload[0],
				selectedCards: action.payload[1],
				matchedCards: action.payload[2],
				gameEnd: action.payload[3]
			}
		case 'SAVE_RESULT':
			return {...state,
				score: action.payload[0],
				saveScore: action.payload[1]
			}
		case 'END_GAME':
			return {...state,
				page: action.payload[0]
			}
		default:
        	return state;
    }
}


