import React from 'react'
import './Score.css'

export class Scoreboard extends React.Component {

    componentDidMount () {
        if (this.props.status === "sended") {
            this.props.loadScoreData();
        };
    };

    componentWillReceiveProps (nextProps) {
        if (nextProps.status === "sended") {
            this.props.loadScoreData();
        };
    };

    prevScore = () => {
        const displayPosition = this.props.displayPosition;
        const scoreCount = this.props.score.length
        this.props.prevScore(displayPosition, scoreCount);
    };

    nextScore = () => {
        const displayPosition = this.props.displayPosition;
        const scoreCount = this.props.score.length
        this.props.nextScore(displayPosition, scoreCount);
    };

    render () {
        let data = this.props.score;
        data.sort((a, b)=>{return b.score - a.score});
        const displayScore = this.props.displayPosition;
        const dataToDisplay = data.slice(displayScore, displayScore + 9);
        let scoreToDisplay = [
        <tr className="scoreElement" key="title" >
        <td>Name</td>
        <td>E-mail</td>
        <td>Score</td>
        </tr>
        ];
        dataToDisplay.forEach((element, i)=>{
            scoreToDisplay.push(
                <tr className="scoreElement" key={i} >
                    <td>{element.username}</td>
                    <td>{element.email}</td>
                    <td>{element.score}</td>
                </tr>
            )
        })

        return (
            <div className="scoreboard">
                <div className="container">
                    <button className='navigation' onClick={this.prevScore.bind(this)} >&#60;</button>
                        <table className='score'>
                            <tbody>
                                {scoreToDisplay}
                            </tbody>
                        </table>
                    <button className='navigation' onClick={this.nextScore.bind(this)} >&#62;</button>
                </div>
            </div>
        )
    }
}


