const initialState = {
    scoreboard: [],
    displayPosition: 0,
    status: false
};

export default function getScore (state = initialState, action) {
    switch (action.type) {
        case 'SCORE_DATA.IS_SENDING':
            return {...state, 
            status: action.payload[0]
        }
        case 'SCORE_DATA.IS_SENDED':
            return {...state, 
            status: action.payload[0]
        }
        case 'SCORE_DATA.IS_LOADING':
            return {...state, 
            status: action.payload[0]
        }
        case 'SCORE_DATA.IS_LOADED':
            return {...state, 
                scoreboard: action.payload[0],
                status: action.payload[1]
        }
        case 'PREVIOUS_SCORE':
            return {...state, 
                displayPosition: action.payload[0]
        }
        case 'NEXT_SCORE':
            return {...state, 
                displayPosition: action.payload[0]
        }
        default:
        	return state;
    }
}


