import ReactDOM from 'react-dom'
import React from 'react'
import {Link} from 'react-router-dom'
import './dataInput.css'

export default class DataInput extends React.Component {
	
	startGame = ()=>{
		const name = document.getElementById('name').value;
		const email = document.getElementById('email').value;
		const cardType = document.getElementById('card_type').value;
		const gameDifficult = document.getElementById('difficult').value;
		const page = 'game';
		this.props.startGame(name, email, cardType, gameDifficult, page);
    }

    componentDidMount = ()=>{
        ReactDOM.findDOMNode(this.refs.name).focus();
    }

    render () {
        return (
            <div className="profile">
                <label htmlFor="name">'Your First Name'</label>
				<input type="text" maxLength="40" placeholder="Name" id="name" ref="name" />
				<label htmlFor="email">'Your email'</label>
				<input type="email" maxLength="40" placeholder="Email" id="email" />
				<Link onClick={this.startGame.bind(this)} to='/game' >Start</Link>
            </div>
        )
    }
}


