import React from 'react'
import {Link} from 'react-router-dom'
import {Timer} from '../timer/timer'
import CardContainer from './cardBoards-item/cardContainer'
import './CardBoard.css'

export class CardBoard extends React.Component {

    componentWillUpdate = (nextProps) => {
        if (nextProps.game.saveScore && nextProps.score.status === false) {
            let data = {
                username: nextProps.user.name,
                email: nextProps.user.email,
                score: nextProps.game.score
            }
            nextProps.pageActions.sendScoreData(data);
        }
    }

    render () {
         if (!this.props.game.saveScore) {
            return (
                <div className="card-board">
                    <div className="container">
                        <Timer game={this.props.game} pageActions={this.props.pageActions} />
                        <CardContainer cards={this.props.game.cards} 
                        cardType={this.props.game.cardType} 
                        cardSelect={this.props.pageActions.cardSelect} 
                        selectCards={this.props.game.selectedCards} 
                        matchedCards={this.props.game.matchedCards} 
                        startTimer={this.props.game.startTimer} />
                    </div>
                </div>
            )} else {
            return (
                <div className="card-board">
                    <div className="container">
                        <h5>Congratulation!</h5>
                        <h5>Your score:</h5>
                        <h4>{this.props.game.score}</h4>
                        <Link to='/scoreboard' onClick={this.props.pageActions.endGame} >Results</Link>
                    </div>
                </div>
            )
        }
        
    }   
}



