import React from 'react'
import {Link} from 'react-router-dom'
import './Header.css'

export class Header extends React.Component {

    startGame = ()=>{

        let name;
        let email;

        if (!document.getElementById('name')) {
            name = this.props.user.name;
		    email = this.props.user.name;
        } else {
            name = document.getElementById('name').value;
            email = document.getElementById('email').value;
        }
		const cardType = document.getElementById('card_type').value;
		const gameDifficult = document.getElementById('difficult').value;
		const page = 'game';
		this.props.startGame(name, email, cardType, gameDifficult, page);
    }

    render () {
        return (
            <header>
                <div className="container">
                    <div className="header_logo">
                        <a href="/">
                            <h1>Match Match Game</h1>
                        </a>
                    </div>
                    <nav id="navigation">
                        <select id="card_type">
                            <option value="1" disabled selected hidden>Card Type</option>
                            <option value="1">First card shirt</option>
                            <option value="2">Second card shirt</option>
                            <option value="3">Third card shirt</option>
                        </select>
                        <select id="difficult">
                            <option value="low" disabled selected hidden>Game Difficulty</option>
                            <option value="low">Low</option>
                            <option value="med">Medium</option>
                            <option value="hard">Hard</option>
                        </select>
                        <Link onClick={this.startGame.bind(this)} to='/game' >Start</Link>
                    </nav>
                </div>
            </header>
        )
    }
};


