import React from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {Header} from '../components/header/Header'
import {LoginScreen} from '../components/loginScreen/loginScreen'
import {CardBoard} from '../components/cardBoard/CardBoard'
import {Scoreboard} from '../components/score/Score'
import {Footer} from '../components/footer/footer'
import {Switch, Route} from 'react-router-dom'
import {getUser, getGame, getScore} from '../selectors/selectors'
import * as pageActions from '../actions/Actions'
import './App.css'
class App extends React.Component {
    render() {
        const {startGame} = this.props.pageActions;
        const user = this.props.user;
        const game = this.props.game;
        const score = this.props.score;
        const pageActions = this.props.pageActions;
        const {loadScoreData} = this.props.pageActions;
        const {prevScore} = this.props.pageActions;
        const {nextScore} = this.props.pageActions;
        const {scoreboard} = this.props.score;
        const {displayPosition} = this.props.score;

        return (
            <section className='game'>
                <Header startGame={startGame} page={this.props.game.page} user={this.props.user} />
                <Switch>
                    <Route exact path='/' render={(props)=>(
                        <LoginScreen {...props} startGame={startGame} />
                    )} />
                    <Route path='/game' render={(props)=>(
                        <CardBoard 
                            {...props} 
                            user={user} 
                            game={game} 
                            score={score} 
                            pageActions={pageActions} 
                        />
                    )} />
                    <Route path='/scoreboard' render={(props)=>(
                        <Scoreboard 
                            {...props} 
                            loadScoreData={loadScoreData} 
                            prevScore={prevScore} 
                            nextScore={nextScore} 
                            score={scoreboard} 
                            displayPosition={displayPosition} 
                            status={score.status}
                        />
                    )} />
                </Switch>
                <Footer />
            </section>
        )
    }
}

function mapStateToProps (state) {
    return {
        user: getUser(state),
        game: getGame(state),
        score: getScore(state)
    }
}

function mapDispatchToProps(dispatch) {
    return {
        pageActions: bindActionCreators(pageActions, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)


